import React from 'react';
import { connect }  from 'react-redux';
import { marketOperation } from '../actions/'



class BuySellButton extends React.Component {

    constructor(props){
        super(props);
        // local state only 
        this.state = {visible: false, msg: 'test'};
    }


    render(){
        
        let position = 0;
        if(this.props.dataReducer.account.position){
            this.props.dataReducer.account.position.forEach( el => { 
                if(el.symbol === this.props.symbol){
                    position = el.qty
                }
            });
        }
        let { cashAvailable, _id } = this.props.dataReducer.account;
        let price = this.props.price;

        if(this.state.visible){    
            return(
                <div className='buy-sell-div' style = {{border: '3px solid #104f55', borderRadius: 5}} >
                            
                            
                    <div 
                        onClick = { ()=> { this.setState({visible: false}) } }
                        className = 'buy-sell-close'
                    >
                        X
                    </div>           
                    
                    <div className='buy-sell-text'>
                        Quantity: {(position)? position : 0}<br/>
                        Cash: {cashAvailable } 
                    </div>

                        <button 
                            className='buy-sell-btn' 
                            onClick={() => {
                                let options = {
                                    _id,
                                    operation: 'buy',
                                    amount: -cashAvailable,
                                    qty: (cashAvailable/price),
                                    symbol: this.props.symbol
                                };
                                console.log("[market] :", options)
                                this.props.onClick(options)}
                            }
                        >
                        All in !!! 🛒
                        </button>
                        <button 
                            disabled={cashAvailable < 500}
                            className='buy-sell-btn' 
                            onClick={() => {
                                let options = {
                                    _id,
                                    operation: 'buy',
                                    amount: -500,
                                    qty: (500/price),
                                    symbol: this.props.symbol
                                };
                                console.log("[market] :", options)
                                this.props.onClick(options)}
                            }
                        >
                        +500$ 👍
                        </button><br/>
                        <button 
                            disabled={position <= 0}
                            className='buy-sell-btn' 
                            onClick={()=>{
                                let options = {
                                    _id,
                                    operation: 'sell',
                                    amount: position*price,
                                    qty: position,
                                    symbol: this.props.symbol
                                };
                                console.log("[market] :", options)
                                this.props.onClick(options)}
                            }
                        >
                        Sell it all 🔥
                        </button>
                        <button 
                            disabled={position * price < 500}
                            className='buy-sell-btn' 
                            onClick={()=>{
                                let options = {
                                    _id,
                                    operation: 'sell',
                                    amount: 500,
                                    qty: (500/price),
                                    symbol: this.props.symbol
                                };
                                console.log("[market] :", options)
                                this.props.onClick(options)}
                            }
                        >
                        -500$ 💸
                        </button>

                <br/>

                {(this.state.msg)?<div className='buy-sell-msg'>{this.state.msg}</div>:null}

                </div>    
            )
        }
     
        else{
            return <div
            className='buy-sell-link'
            onClick = { ()=> { this.setState({visible: true}) }}
            style = {{ cursor: 'pointer' }}
            >buy/sell</div>
        }
    }
}

const mapStateToProps = state => state

const mapDispatchToProps = dispatch => {
    return {
        onClick: (options) => dispatch( marketOperation(options) )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BuySellButton)


